import blog from '../../../src/api/blog'
import {mapGetters} from 'vuex'

export default {
  name: 'HelloWorld',
  data () {
    return {
      page: null,
      total: null,
      totalPage: '',
      blogs: []
    }
  },
  created(){
    this.page = parseInt(this.$route.query.page) || 1
    blog.getBlogsByUserId(this.user.id, {page: this.page}).then(res=>{
      //console.log(res);
      let {page, total, totalPage, data} = res
      this.page = page
      this.total = total
      this.totalPage = totalPage
      this.blogs = data
    })
  },
  computed: {
    ...mapGetters(['user'])
  },
  methods: {
    splitDate(dateStr){
      let date = typeof dateStr === 'object' ? dateStr : new Date(dateStr)
      let day = date.getDate()
      let month = date.getMonth() + 1
      let year = date.getFullYear()
      return {day,month,year}
    },
    onPageChange(newPage){
      blog.getBlogsByUserId(this.user.id,{page: newPage}).then(res=>{
        console.log(res);
        this.page = res.page
        this.total = res.total
        this.totalPage = res.totalPage
        this.blogs = res.data
        console.log(this.blogs);
        this.$router.push({path: `/my`, query: {page: newPage}})
      })
    },
    onDelete(blogId){
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        center: true
      }).then(()=>{
        blog.deleteBlog({blogId})
      }).then(() => {
        this.$message({
          type: 'success',
          message: '删除成功!'
        });
        this.blogs = this.blogs.filter(blog=> blog.id !== blogId)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    }
  }
}



